
* {
    margin: 0; 
    padding: 0;
}
body {
    font:12px/1.5 Tahoma, Helvetica, Arial, "Microsoft YaHei", "Hiragino GB", Sans-serif;
}
ul,ol {
    list-style-type: none;
}
a {
    text-decoration: none;
}
a:link {
    color: #000;
}
a:hover {
    color: #0000A0;
}
a:visited {
    text-decoration: underline;
    color: #00F;
}
a img, 
div img {
    vertical-align: top;
}
*:before,
*:after {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
/* 宽度要与图片相等 */
#slideshow {
    max-width: 1024px;
    margin-bottom: 100px;
}




























































































































































































/*过渡的类不能合并在一起写*/
.from-right-enter-active[data-v-6d202304] {
	-webkit-transition: all .5s;
	transition: all .5s;
}
.from-right-enter[data-v-6d202304] {
	-webkit-transform: translateX(100%);
	        transform: translateX(100%);
}
.to-left-leave-active[data-v-6d202304] {
	-webkit-transition: all .5s;
	transition: all .5s;
}
.to-left-leave-to[data-v-6d202304] {
	-webkit-transform: translateX(-100%);
	        transform: translateX(-100%);
}
.from-left-enter-active[data-v-6d202304] {
	-webkit-transition: all .5s;
	transition: all .5s;
}
.from-left-enter[data-v-6d202304] {
	-webkit-transform: translateX(-100%);
	        transform: translateX(-100%);
}
.to-right-leave-active[data-v-6d202304] {
	-webkit-transition: all .5s;
	transition: all .5s;
}
.to-right-leave-to[data-v-6d202304] {
	-webkit-transform: translateX(100%);
	        transform: translateX(100%);
}
#slideshow[data-v-6d202304] {
    position: relative;   /* 如何解决高度自适应问题 */
}
.slideshow-pics[data-v-6d202304] {             
    width: 100%;
    /* 子元素为绝对定位，要实现overflow:hidden，父元素要设置relative*/
    position: relative;             
    overflow: hidden;
}
.slideshow-pics img[data-v-6d202304] {
	position: absolute;
	width: 100%;
}
.pre-button[data-v-6d202304],
.next-button[data-v-6d202304] {
	position: absolute;
    top: 0;
    width: 5%;
    height: 100%;
    text-indent: 100%;        /*略去文字显示*/
    overflow: hidden;
    background: #ccc;
    opacity:0.5;
    cursor: pointer;
}
.pre-button[data-v-6d202304] {
	left: 0;
}
.next-button[data-v-6d202304] {
	right: 0;
}
.pre-button[data-v-6d202304]:after,
.next-button[data-v-6d202304]:after {
	position: absolute;
	left: 0;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	 /* 伪元素不是block，但可以设置宽度等于父元素100% */
	width: 100%;              
	font-size: 50px;
	text-indent: 0;
	text-align: center;
	color: #fff;
}
.pre-button[data-v-6d202304]:after {
    content: "\FF1C";
}
.next-button[data-v-6d202304]:after {
    content: "\FF1E";
}
.choose-button[data-v-6d202304] {
	position: absolute;
	left: 5%;
	bottom: 0;
	/* 移到幻灯窗底部 */
	-webkit-transform: translateY(120%);
	transform: translateY(120%);
}
.choose-button li[data-v-6d202304] {
	display: inline-block;
	vertical-align: top;
	margin-right: 15px;
	padding-right: 10px;
	font: 13px/1 "Tahoma";
	border-right: 1px dotted #ccc;
	cursor: pointer;
}
.choose-button li[data-v-6d202304]:last-child {
	border-right: 0;
}
.choose-button li[data-v-6d202304]:hover {
	background-color: #e66c2c;
}
.choose-button-selected[data-v-6d202304] {
    border-bottom: 2px solid blue;
}
